import React from 'react'
import { Container, Typography, Button, Grid } from '@material-ui/core'
import { Link } from 'react-router-dom'

import CartItem from './CartItem/CartItem'
import useStyles from './styles'

const noItemsMessage = 'You have no items in your shopping cart'

const Cart = ({ cart, onUpdateCartQty, onRemoveFromCart, onEmptyCart }) => {
  const classes = useStyles()
  const handleEmptyCart = () => onEmptyCart()

  const renderEmptyCart = () => (
    <Typography variant="subtitle1">{noItemsMessage}.
      <Link className={classes.link} to="/"> <br/>Add Items</Link>
    </Typography>
  );

  if (!cart.line_items) return 'Loading'
  const isEmpty = !cart.line_items.length

  const renderCart = () => (
    <>
      <Grid container spacing={3}>
        {cart.line_items.map((lineItem) => (
          <Grid item xs={12} sm={4} key={lineItem.id}>
            <CartItem
              item={lineItem}
              onUpdateCartQty={onUpdateCartQty}
              onRemoveFromCart={onRemoveFromCart}
            />
          </Grid>
        ))}
      </Grid>
      <div className={classes.cardDetails}>
        <Typography variant="h4">Subtotal: {cart.subtotal.formatted_with_symbol}</Typography>
        <div>
          <Button className={classes.emptyButton} size="large" type="button" variant="contained" onClick={handleEmptyCart}>Empty cart</Button>
          <Button className={classes.checkoutButton} component={Link} to="/checkout" size="large" type="button" variant="contained">Checkout</Button>
        </div>
      </div>
    </>
  );
  return (
    <Container>
      <div className={classes.toolbar} />
      <Typography className={classes.title} variant="h3" gutterBottom>Your Shopping Cart</Typography>
      { isEmpty ? renderEmptyCart() : renderCart() }
    </Container>
  );
};
export default Cart;